<template>
  <div class="page">
    <index-top 
      :h1="'KISwap'"
      :h4="$t('index.最具潜力的集DEX、IKO、DAO、聚合交易为一体的DEFI生态平台')"></index-top>
    <div class="reward-info flex-center">
      <div class="text">{{$t('trading.当前池子总奖励：')}}<span class="second-color">{{totalKisAmount}} KIS</span></div>
      <div class="text">{{$t('trading.当前个人可提现奖励：')}}<span class="second-color">{{userTotalAmount}}KIS</span></div>
    </div>
    <div class="add-btn" v-if="userTotalAmount > 0" @click="takerWithdraw">{{$t('trading.提现奖励')}}</div>
    <div class="add-btn disabled" v-else>{{$t('trading.提现奖励')}}</div>
    <div class="reward-pro flex-wrap ">
      <div class="pro-item" v-for="item in poolPairList" @click="toSwap(item)">
        <span class="icon-sign">{{totalAllocPoint ? Number(item.allocPoint *100 / totalAllocPoint).toFixed(2) : 0.00}}%</span>
        <div class="pro-top flex-center">
          <img :src="$tokenLogo(item.tokenA)" alt="" class="icon-logo">
          <img src="@/assets/image/light/trading/tra.png" alt="" class="icon-tra" v-if="themeMode === 'light'">
          <img src="@/assets/image/trading/tra.png" alt="" class="icon-tra" v-else>
          <img :src="$tokenLogo(item.tokenB)" alt="" class="icon-logo">
        </div>
        <div class="pro-tit">{{item.symbol}}</div>
        <div class="pro-info">
          <div class="row flex-middle">
            <div class="label">{{$t('trading.收益率（日/年）')}}*:</div>
            <div class="flex-item text-r cont">{{rate && totalAllocPoint ? Number(item.allocPoint * totalMintDay / totalAllocPoint / 28800 / rate).toFixed(2) : 0}}%/{{rate && totalAllocPoint ? Number(item.allocPoint * totalMintDay * 365 / totalAllocPoint / 28800 / rate).toFixed(2) : 0}}%</div>
          </div>
          <div class="row flex-middle">
            <div class="label">{{$t('trading.当前总奖励')}}:
              <el-popover
                placement="right"
                :width="200"
                trigger="hover"
                :content="$t('trading.当前该池子可分配的总奖励')">
                <template #reference><span class="icon-pop"></span></template>
              </el-popover>
            </div>
            <div class="flex-item text-r cont">{{Number(item.kisTotal).toFixed(3)}}</div>
          </div>
          <!-- <div class="row flex-middle">
            <div class="label">{{$t('trading.交易预算')}}:
              <el-popover
                placement="right"
                width="200"
                trigger="hover"
                :content="$t('trading.当前该池子可分配的总奖励')">
                <template #reference><span class="icon-pop"></span></template>
              </el-popover>
            </div>
            <div class="flex-item text-r cont">$7,376,001,569.78</div>
          </div> -->
          <div class="row flex-middle">
            <div class="label">{{$t('trading.当前交易额')}}:
              <el-popover
                placement="right"
                :width="200"
                trigger="hover"
                :content="$t('trading.当前该池子的交易额，即该池子暂未提取奖励的交易额')">
                <template #reference><span class="icon-pop"></span></template>
              </el-popover>
            </div>
            <div class="flex-item text-r cont">{{Number(item.usdtTotal).toFixed(3)}} USDT</div>
          </div>
          <div class="row flex-middle">
            <div class="label">{{$t('trading.个人交易额')}}:
              <el-popover
                placement="right"
                :width="200"
                trigger="hover"
                :content="$t('trading.你在该池子的交易额，如果提取奖励后，交易额清零')">
                <template #reference><span class="icon-pop"></span></template>
              </el-popover>
            </div>
            <div class="flex-item text-r cont">{{Number(item.userTotal).toFixed(2)}}USDT（{{Number(item.usdtTotal) ? Number(item.userTotal/item.usdtTotal).toFixed(3) * 100 : '0.00'}}%）</div>
          </div>
          <div class="row flex-middle">
            <div class="label">{{$t('trading.个人奖励')}}:
              <el-popover
                placement="right"
                :width="200"
                trigger="hover"
                :content="$t('trading.个人交易额/当前交易额，根据你占池子的份额，增加或减少')">
                <template #reference><span class="icon-pop"></span></template>
              </el-popover>
            </div>
            <div class="flex-item text-r cont">{{Number(item.userReward).toFixed(5)}}KIS</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import IndexTop from "@/views/index/IndexTop";
import { reactive, toRefs, computed, watch} from "vue";
import swapAbi from "@/utils/swapAbi";
import {useStore} from "vuex";
import swapList from "@/utils/swapList";
import config from "@/utils/config";
import BigNumber from "bignumber.js";
import tokenAbi from "@/utils/abi";
export default {
  components: {
    IndexTop
  },
  setup () {
     const store = useStore();
     const data = reactive({
       themeMode: computed(() => store.state.themeMode),
       poolPairList: swapList,
       rate: computed(() => store.state.rate),
       web3: computed(() => store.state.provider),
       poolTotalLock: 0,
       totalAllocPoint: 0,
       totalMintDay: 0,
       totalMint: 0,
       totalKis: [],
       userTotal: [],
       totalKisAmount: computed(() => {
         let amount = 0;
         data.totalKis.forEach((item) => {
           amount = new BigNumber(amount).plus(item)
         })
         return amount.toFixed(5)
       }),
       userTotalAmount: computed(() => {
         let amount = 0;
         data.userTotal.forEach((item) => {
           amount += Number(item)
         })
         return amount
       }),
       address: computed(() => store.state.accounts[0]),
       poolAddress: config.swapMingAddress,
       isLp: true,
       toggleLP: () => {
         data.isLp = !data.isLp
       },
       getPoolReward: () => {
         let poolContract = new data.web3.eth.Contract(swapAbi, data.poolAddress)
         poolContract.methods.totalAllocPoint().call().then((result) => {
           data.totalAllocPoint = result
           console.log('totalAllocPoint',result)
         })
         data.web3.eth.getBlockNumber().then((block) => {
            poolContract.methods.getMdxReward(block).call().then(async (result) => {
              let startBlock = await poolContract.methods.startBlock().call()
              data.totalMint = (block - startBlock) * result / Math.pow(10, 18)
              data.totalMintDay = result * 28800 / Math.pow(10, 18)
            })
         })
       },
       getPoolInfo: () => {
         data.totalKis = []
         data.userTotal = []
         let poolContract = new data.web3.eth.Contract(swapAbi, data.poolAddress)
         data.poolPairList.forEach(async (item) => {
            let poolInfo = await poolContract.methods.getPoolInfo(item.pid).call()
            let userReword = await poolContract.methods.getUserReward(item.pid).call({from: data.address})
            item.usdtTotal = Number(poolInfo[3]/Math.pow(10,10)).toFixed(18)
            item.kisTotal = Number(poolInfo[2]/Math.pow(10,18)).toFixed(18)
            item.userTotal = Number(userReword[1]/Math.pow(10, 10)).toFixed(18)
            item.userReward = Number(userReword[0]/Math.pow(10, 18)).toFixed(18)
            data.totalKis.push(item.kisTotal)
            data.userTotal.push(item.userReward)
         });
       },
       takerWithdraw: async () => {
         let poolContract = new data.web3.eth.Contract(swapAbi, data.poolAddress)
         await poolContract.methods.takerWithdraw().send({from: data.address})
         data.getPoolReward()
         data.getPoolInfo()
       },
       toSwap: (item) => {
         if (item.tokenB == config.woktAddress) {
           item.tokenB = 'OKT'
         }
         if (item.tokenA == config.woktAddress) {
           item.tokenA = 'OKT'
         }
         window.location.href = `https://spadesj.github.io/kis/app2/#/swap?inputCurrency=${item.tokenA}&outputCurrency=${item.tokenB}`
       }
     })
     if (data.address) {
        data.getPoolReward()
        data.getPoolInfo()
     }
     watch(() => data.address, (val) => {
       if (val) {
         data.getPoolReward()
         data.getPoolInfo()
       }
     })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="scss" scoped>
.add-btn {
  display: block;
  width: 338px;
  height: 55px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  text-align: center;
  line-height: 55px;
  margin: 40px auto;
  background: url('~@/assets/image/liquidity/btn_bg5.png') no-repeat center;
  background-size: 100% 100%;
  cursor: pointer;
  transition: all .3s;
  &:hover {
    background-image: url('~@/assets/image/liquidity/btn_bg6.png');
  }
  &.disabled {
    color: #AAB0B5;
    background-image: url('~@/assets/image/liquidity/btn_bg5_dis.png');
  }
}
