    }
  }
  .reward-pro {
    .pro-item {
      background-image: url('~@/assets/image/light/trading/borderbg1.png');
      .icon-sign {
        color: #AD7805;
        background-image: url("~@/assets/image/light/trading/sign.png");
      }
      .pro-tit {
        color: #333;
      }
      .pro-info {
        .row {
          box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.08);
