        font-size: 14px;
      }
      .pro-top {
        .icon-logo {
          width: 44px;
          height: 44px;
        }
        .icon-tra {
          width: 32px;
        }
      }
      .pro-tit {
        font-size: 22px;
        margin: 18px 0 12px;
      }
